import React, { useEffect, useState, useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import axios from 'axios'
import { Container, Row, Col, Button, Table } from 'react-bootstrap'
import './checkout.css'
import { toast, Zoom } from 'react-toastify'
import { useNavigate } from 'react-router-dom'
import { selectSucccess } from '../Redux/authSlice'
import { clearcart } from '../Redux/cartSlice'
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

const Checkout = () => {
    const sleep = (milliseconds) => {
        return new Promise(resolve => setTimeout(resolve, milliseconds))
    }
    const isLogin = useSelector(selectSucccess)
    const dispatch = useDispatch()
    const dataFetch = useRef(false)
    const [email, setEmail] = useState(null)
    const token = localStorage.getItem('token')
    const navigate = useNavigate()
    const cartList = useSelector((state) => state.cart)
    const [isLoading, setLoading] = useState(false);
    const [data, setData] = useState({
        customerName: '',
        adress: '',
        phone: '',
        products: []
    })
    const [payMethod, setPayMethod] = useState("1")
    const TotalPriceProduct = () => {
        var sum = 0;
        for (let i = 0; i < cartList.length; i++) {
            sum = sum + cartList[i].product.price * cartList[i].quality
        }
        return sum
    }
    console.log(data)
    useEffect(() => {
        if (dataFetch.current)
            return
        for (let i = 0; i < cartList.length; i++) {
            console.log(i)
            data.products.push({
                id: cartList[i].product.id,
                size: cartList[i].size,
                qty: cartList[i].quality,
                price: cartList[i].product.price
            })
        }
        dataFetch.current = true
    }, [])
    useEffect(() => {
        if (!isLogin) {
            navigate('/')
            return
        }
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth',
        });
        axios.get("https://localhost:7164/api/Accounts/profile/user", {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then(res => {
                setData({
                    ...data,
                    customerName: res.data.name,
                    // adress: res.data.adress,
                    phone: res.data.phonenumber
                })
                console.log(res.data)
                setEmail(res.data.email)
            }).catch(error => {
                console.log(error)
            })
    }, [isLogin])
    const changeHandler = (event) => {

        setData({ ...data, [event.target.name]: event.target.value });
    };
    const handleOrder = async (e) => {
        e.preventDefault();

        setLoading(true)
        const priceALL = TotalPriceProduct()
        await sleep(500);
        if (!data.adress || !data.customerName || !data.phone) {
            toast.success("in  thng tin!", {
                position: toast.POSITION.TOP_CENTER,
                autoClose: 1000,
                pauseOnFocusLoss: true,
                transition: Zoom,
                role: "alert"
            })
            setLoading(false)
            return
        }
        if (payMethod == "2") {
            const data2 = {
                ...data,
                payMethod: "2",
                amount: priceALL.toString()
            }
            console.log(data2)
            axios.post("https://localhost:7164/api/Order/Checkout", data2, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            })
                .then(async res =>  {
                    console.log(res.data)

                    // navigate('/cart/order')
                    // toast.success("t hng thnh cng", {
                    //     position: toast.POSITION.TOP_CENTER,
                    //     autoClose: 1000,
                    //     pauseOnFocusLoss: true,
                    //     transition: Zoom,
                    //     role: "alert"
                    // })
                    await dispatch(clearcart())
                    setLoading(false);
                    window.location.replace(res.data.urlPayment);
                })
                .catch(errors => {
                    if (!errors?.response) {
                        toast("No respon server")

                    } else if (errors.response?.status === 400) {
                        toast("error")

                    } else if (errors.response?.status === 430) {
                        toast("Ht size ri !!!!")

                    } else {
                        toast("error")

                    }
                    setLoading(false)
                })
        }
        else {
            axios.post("https://localhost:7164/api/Order/Checkout", data, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            })
                .then(res => {
                    console.log(res.data)

                    setLoading(false);
                    navigate('/cart/order')
                    toast.success("t hng thnh cng", {
                        position: toast.POSITION.TOP_CENTER,
                        autoClose: 1000,
                        pauseOnFocusLoss: true,
                        transition: Zoom,
                        role: "alert"
                    })
                    dispatch(clearcart())
                })
                .catch(errors => {
                    if (!errors?.response) {
                        toast("No respon server")

                    } else if (errors.response?.status === 400) {
                        toast("error")

                    } else if (errors.response?.status === 430) {
                        toast("Ht size ri !!!!")

                    } else {
                        toast("error")

                    }
                    setLoading(false)
                })
        }
    }
    // api get tinh
    const [provinces, setProvinces] = useState([])
    const [province_id, setProvince_id] = useState(-1)
    const [districts, setDistricts] = useState([])
    const [district_id, setDistrict_id] = useState(-1)
    const [wards, setWards] = useState([])
    const [ward_id, setWard_id] = useState(-1)
    const [ap_number, setAp_number] = useState('')
    useEffect(() => {
        axios.get('https://vapi.vnappmob.com/api/province/')
            .then(res => {
                setProvinces(res.data.results)
            })
            .catch(error => console.log(error))
    }, [])
    useEffect(() => {
        if (province_id === -1) {
            setDistrict_id(-1)
            return
        }
        axios.get(`https://vapi.vnappmob.com/api/province/district/${province_id}`)
            .then(res => {
                setDistrict_id(-1)
                setDistricts(res.data.results)
            })
            .catch(error => console.log(error))
    }, [province_id])
    useEffect(() => {
        if (district_id === -1 || province_id === -1) {
            setWard_id(-1)
            return
        }
        axios.get(`https://vapi.vnappmob.com/api/province/ward/${district_id}`)
            .then(res => {
                setWard_id(-1)
                setWards(res.data.results)
            })
            .catch(error => console.log(error))
    }, [district_id, province_id])

    const [address, setAddress] = useState('')
    useEffect(() => {
        if (ward_id > 0) {
            var p1 = ''
            var p2 = ''
            var p3 = ''
            p1 = provinces?.find((p) => p.province_id == province_id)
            p2 = districts?.find((p) => p.district_id == district_id)
            p3 = wards?.find((p) => p.ward_id == ward_id)
            if (!ap_number) {
                setData({ ...data, adress: ap_number + p3?.ward_name + ' - ' + p2?.district_name + ' - ' + p1?.province_name })
            }
            else
                setData({ ...data, adress: ap_number + ' - ' + p3?.ward_name + ' - ' + p2?.district_name + ' - ' + p1?.province_name })
        } else {
            setData({ ...data, adress: '' })
        }

    }, [district_id, province_id, ward_id, ap_number])
    console.log(payMethod)
    // console.log(address)

    return (
        <Container>
            <div className='checkout'>
                <Row xs={1} md={1} xl={2} className=''>
                    <Col xl={7} className='checkout__info c-flex g-2 pl-1'  >
                        <p className='checkout__info-title'>Kim tra thng tin:</p>
                        <div className='checkout__adress__final'>
                            <p>Ngi nhn: {data.customerName}</p>
                            <p>ST: 0367570800</p>
                            <p>Giao hng ti:{data.adress && <> {data.adress}</>}</p>
                        </div>

                        <div className='checkout__info-desc'>
                            <label>H tn:</label>
                            <input
                                className='input'
                                type="text"
                                name="customerName"
                                value={data.customerName}
                                onChange={changeHandler}
                                placeholder="H tn"
                            />
                        </div>
                        <div className='checkout__info-desc'>
                            <label>Email:</label>
                            <input
                                className='input'
                                type="text"
                                name="email"
                                value={email}
                                placeholder="Email"
                            />
                        </div>
                        <div className='checkout__info-desc'>
                            <label>S in thoi:</label>
                            <input
                                className='input'
                                type="text"
                                name="phone"
                                value={data.phone}
                                onChange={changeHandler}
                                placeholder="phoneNumber"
                            />
                        </div>
                        <div className='checkout__info-desc'>
                            <label className='checkout__adress'>a ch:</label>
                            <Row className='g-4'>
                                <Col>
                                    <select id="provinces" name="provincesList" value={province_id} form="provincesForm" onChange={(e) => setProvince_id(e.target.value)}>
                                        <option value={-1}>Tnh/Thnh Ph</option>
                                        {provinces?.map((item, index) => (
                                            <option key={item.province_id} value={item.province_id}>{item.province_name}</option>
                                        ))}
                                    </select>
                                </Col>
                                <Col>
                                    <select disabled={province_id < 0 ? true : false} id="districts" value={district_id} name="districtsList" form="districtsForm" onChange={(e) => setDistrict_id(e.target.value)}>
                                        <option value={-1}>Qun/Huyn</option>
                                        {districts?.map((item, index) => (
                                            <option key={item.district_id} value={item.district_id}>{item.district_name}</option>
                                        ))}
                                    </select>
                                </Col>

                                <Col>
                                    <select disabled={district_id < 0 ? true : false} id="wards" value={ward_id} name="wardsList" form="wardsForm" onChange={(e) => setWard_id(e.target.value)} >
                                        <option value={-1}>Phng/X</option>
                                        {wards?.map((item, index) => (
                                            <option key={item.ward_id} value={item.ward_id}>{item.ward_name}</option>
                                        ))}
                                    </select>
                                </Col>
                            </Row>
                            <input
                                className='input'
                                type="text"
                                value={ap_number}
                                onChange={(e) => setAp_number(e.target.value)}
                                placeholder="a ch c th ( s nh,...)"
                            />
                            {/* <input
                                className='input'
                                disabled='true'
                                type="text"
                                name="adress"
                                value={data.adress}
                            /> */}
                        </div>

                    </Col>
                    <Col xl={5} className='checkout__order'>
                        <p className='checkout__info-order'>Thng tin sn phm:</p>
                        <Table striped bordered hover>
                            <thead>
                                <tr>
                                    <th>Stt</th>
                                    <th>Name</th>
                                    <th>Size</th>
                                    <th>Price</th>
                                    <th>Sl</th>
                                </tr>
                            </thead>
                            <tbody>
                                {cartList.map((item, index) => (
                                    <tr key={index}>
                                        <td>{index + 1}</td>
                                        <td>{item.product.name}</td>
                                        <td>{item.size}</td>
                                        <td>{item.product.price.toLocaleString('it-IT', { style: 'currency', currency: 'VND' })}</td>
                                        <td>{item.quality}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                        <br />
                        <div className="bill">
                            <div className='checkout__order-price'><p>Tng tin sn phm: </p><p>{TotalPriceProduct().toLocaleString('it-IT', { currency: 'VND' })}</p></div>
                            <div className='checkout__order-price'><p>Gim gi (Voucher): </p><p>- 0</p></div>
                            <div className='checkout__order-price'><p>Ph vn chuyn: </p><p>0</p></div>
                            <div className='checkout__order-price total__price'><p>Tng thanh ton: </p><p>{TotalPriceProduct().toLocaleString('it-IT', { currency: 'VND' })}</p></div>
                        </div>
                        <div className='pick_paymethod'>Chn phng thc thanh ton</div>
                        {/* <select className="paymethod" value={payMethod} name="districtsList" form="districtsForm" onChange={(e) => setPayMethod(e.target.value)}>
                            <option value={"1"}>Thanh ton khi nhn hng</option>
                            <option value={"2"}>Thanh ton qua v VNPay</option>
                        </select> */}

                        <FormControl sx={{ m: 1, minWidth: 120 }} size="larget">
                            <Select
                                // id="demo-select-small"
                                value={payMethod}
                                onChange={(e) => setPayMethod(e.target.value)}
                            >
                                <MenuItem value={"1"}>Thanh ton khi nhn hng</MenuItem>
                                <MenuItem value={"2"}>Thanh ton qua v VNPay</MenuItem>

                            </Select>
                        </FormControl>
                        <Button
                            className='checkout__order-btn'
                            disabled={isLoading}
                            onClick={!isLoading ? handleOrder : null}
                        >
                            {isLoading ? 'Loading...' : 't hng'}
                        </Button>
                    </Col>



                </Row>
            </div>

        </Container>

    )
}

export default Checkout
